.sidebar-container {
  max-width: 348px;
  width: 100%;
  &__profile {
    @include centralize-content;
    border-radius: 20px;
    padding: 32px 32px 48px;
    width: 100%;
    background-color: $secondary;

    &__avatar {
      width: 128px;
      height: 128px;
      overflow: hidden;
      border: 2px solid $green;
      border-radius: 50%;
      display: flex;
      &__image {
        width: 100%;
        height: auto;
      }
    }
    &__name {
      &__area {
      }
    }
  }
}
